.container {
    height: 350px;
    background-color: color-mix(in srgb, rgba(0, 0, 255, 0.719) 50%, grey 50%);
  }
  
  .photo {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 100px;
    left: 250px;
  }
  
  .profile {
    position: absolute;
    top: 130px;
    left: 500px;
    color: orange;
  }
  
  .profile h3 {
    display: inline-block;
    position: relative;
  }
  
  .profile h3::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px; /* Adjust the height of the cursor line as needed */
    background-color: #3498db; /* Adjust the color of the cursor line */
    bottom: 0;
    left: 0;
    transform-origin: bottom right;
    animation: cursorAnimation 1s infinite; /* Adjust animation duration as needed */
  }
  
  @keyframes cursorAnimation {
    0% {
      transform: scaleX(0);
    }
    50% {
      transform: scaleX(1);
    }
    100% {
      transform: scaleX(0);
    }
  }
  .container {
    background: linear-gradient(to right, #3498db, #e74c3c);
    background-size: 200% 200%;
    animation: gradientAnimation 5s infinite;
  }
  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }